	module.exports = {"data":{"markdownRemark":{"html":"<p>Most HTML elements have an opening tag and a closing tag.</p>\n<p>Opening tags look like this: <code>&#x3C;h1></code> and Closing tags look like this: <code>&#x3C;/h1></code>.</p>\n<p>Note that the only difference between opening and closing tags is that closing tags have a slash after their opening angle bracket.</p>\n<p>There are also some HTML elements that are self-closing.</p>\n<p>For example, the image tag <code>&#x3C;img /></code> and the input tag <code>&#x3C;input /></code>.</p>\n<p><a href='http://www.freecodecamp.com/challenges/say-hello-to-html-elements' target='_blank' rel='nofollow'>Try it here!</a></p>","fields":{"slug":"/miscellaneous/html-elements/"},"frontmatter":{"title":"HTML Elements"}}},"pathContext":{"slug":"/miscellaneous/html-elements/"}}

